export default {
  data() {
    return {
      size: 'small',
      imageUrl: '',
      user: sessionStorage.getItem("user"),
      centerDialogVisible: false,//
      editDialogVisible: false, // 
      editLoading: false,
      dataFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      dataForm: {
        id: 0,
        label: '',
        value: '',
        type: '',
        sort: 0,
        description: '',
        remarks: ''
      }
    };
  },
  methods:{
    toHistory: function() {
      if (this.user!=null){
        this.$router.push({path:'/history'});
      }else{
        this.$confirm("", "", {
          type: "warning"
        })
          .then(() => {
            this.$router.push({path:'/login'});
          })
          .catch(() => {

          })
      }
    },
    toCall(){
      this.$router.push({path:'/call'});
    },
    // 
    logout: function() {
      this.$confirm("?", "", {
        type: "warning"
      })
      .then(() => {
        this.$router.push({path:'/login'})
        sessionStorage.removeItem("user")
        sessionStorage.removeItem("themeColor")
        this.deleteCookie("token")
      })
      .catch(() => {})
    },
    // cookie
    deleteCookie: function(name) {
      Cookies.remove(name)
    },
    updateUser(){
      this.$router.push({path:'/updateUser'})
    },
    // 
    handleEdit: function () {
      this.editDialogVisible = true
      this.$api.user.findByName({"name":sessionStorage.getItem("user")}).then((res) => {
        if(res.code == 200) {
          this.dataForm = Object.assign({}, res.data)
        } else {
          this.$message({message: ', ' + res.msg, type: 'error'})
        }
      })
    },
    // 
    submitForm: function () {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {}).then(() => {
            this.editLoading = true
            let params = Object.assign({}, this.dataForm)
            this.$api.dict.save(params).then((res) => {
              if(res.code == 200) {
                this.$message({ message: '', type: 'success' })
              } else {
                this.$message({message: ', ' + res.msg, type: 'error'})
              }
              this.editLoading = false
              this.$refs['dataForm'].resetFields()
              this.editDialogVisible = false
              this.findPage(null)
            })
          })
        }
      })
    },
    // 
    handleRemove() {
      this.imageUrl = ''
    },
    // 
    handleAvatarSuccess(res, file) {
      this.imageUrl = res.data.url
    },
    // 
    beforeAvatarUpload(file) {
      const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!type) {
        this.$message.error('!(jpgpngwebpJPEG)')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return type && isLt2M
    }
  }
}



